import React from "react";
import styled from "styled-components";

const Table = styled.div`
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto auto auto auto auto;
  h2 {
    grid-area: 1 / 1 / 2 / 3;
  }
  h2.bottom {
    grid-area: 8 / 1 / 9 / 3;
  }
  p {
    grid-area: 2 / 1 / 3 / 3;
    margin: 10px;
  }
  p.item-1 {
    grid-area: 3 / 1 / 4 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-1_2 {
    grid-area: 3 / 2 / 4 / 3;
  }
  p.item-2 {
    grid-area: 4 / 1 / 5 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-2_2 {
    grid-area: 4 / 2 / 5 / 3;
  }
  p.item-3 {
    grid-area: 5 / 1 / 6 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-3_2 {
    grid-area: 5 / 2 / 6 / 3;
  }
  p.item-4 {
    grid-area: 6 / 1 / 7 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-4_2 {
    grid-area: 6 / 2 / 7 / 3;
  }
  p.item-5 {
    grid-area: 7 / 1 / 8 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-5_2 {
    grid-area: 7 / 2 / 8 / 3;
  }
`;

export default () => (
  <Table>
    <h2>Как проходит мастер класс</h2>
    <p>
      Продолжительность - 45 минут <br />
      Ход мастер-класса:
    </p>
    <p className="item-1">Знакомство</p>
    <p className="item-1_2">
      Знакомим учеников с сюжетом урока и детей друг с другом и с
      преподавателями
    </p>
    <p className="item-2">О Python</p>
    <p className="item-2_2">
    Рассказываем о языке программирования Python: где и как используется, что с его помощью можно сделать
    </p>
    <p className="item-3">Задания на платформе</p>
    <p className="item-3_2">
    Программируем анимированные эмоджи и составляем из них ребус, загадывая любимого супер-героя, который впоследствии нужно угадать одногруппникам
    </p>
    <p className="item-4">Рефлексия</p>
    <p className="item-4_2">
    Обсуждаем практическую ценность программирования в связке с реальной жизнью
    </p>
    <h2 className="bottom">Посмотрите живой отзыв от нашей ученицы</h2>
  </Table>
);
