import React from "react";
import styled from "styled-components";

const Table = styled.div`
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto auto auto auto auto;
  h2 {
    grid-area: 1 / 1 / 2 / 3;
  }
  h2.bottom {
    grid-area: 8 / 1 / 9 / 3;
  }
  p {
    grid-area: 2 / 1 / 3 / 3;
    margin: 10px;
  }
  p.item-1 {
    grid-area: 3 / 1 / 4 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-1_2 {
    grid-area: 3 / 2 / 4 / 3;
  }
  p.item-2 {
    grid-area: 4 / 1 / 5 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-2_2 {
    grid-area: 4 / 2 / 5 / 3;
  }
  p.item-3 {
    grid-area: 5 / 1 / 6 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-3_2 {
    grid-area: 5 / 2 / 6 / 3;
  }
  p.item-4 {
    grid-area: 6 / 1 / 7 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-4_2 {
    grid-area: 6 / 2 / 7 / 3;
  }
  p.item-5 {
    grid-area: 7 / 1 / 8 / 2;
    @media (max-width: 607px) {
      display: none;
    }
  }
  p.item-5_2 {
    grid-area: 7 / 2 / 8 / 3;
  }
`;

export default () => (
  <Table>
    <h2>Как проходит мастер класс</h2>
    <p>
      Продолжительность - 45 минут <br />
      Ход мастер-класса:
    </p>
    <p className="item-1">Знакомство</p>
    <p className="item-1_2">
      Знакомим учеников с сюжетом урока и детей друг с другом и с
      преподавателями
    </p>
    <p className="item-2">Обсуждение правил безопасности</p>
    <p className="item-2_2">
      Знакомимся с основными правилами безопасности в Сети и предложить детям
      придумать по одному своему правилу
    </p>
    <p className="item-3">Работа в граф. редакторе</p>
    <p className="item-3_2">
      Создаем свою GIF-анимацию в графическом редакторе
    </p>
    <p className="item-4">Загрузка гифки</p>
    <p className="item-4_2">
      Сохраняем результат, делимся с группой, обсуждаем у кого что получилось,
      что понравилось, что хотелось бы еще сделать
    </p>
    <p className="item-5">Игра на интернетбезопасность </p>
    <p className="item-5_2">Закрепляем теоретические знания с помощью игры</p>
    <h2 className="bottom">Посмотрите живой отзыв от нашей ученицы</h2>
  </Table>
);
